import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

import PageHeader from '../../components/PageHeader';
import PageFooter from '../../components/PageFooter';

function AboutUs() {
    const [scroll, setScroll] = useState('hidden');
    useEffect(() => {
        const scrollListener = () => {
            if (window.scrollY < 60) {
                setScroll(true);
            } else {
                setScroll(false);
            }
        };
        window.addEventListener("scroll", scrollListener);

        return () => {
            window.removeEventListener("scroll", scrollListener);
        };
    }, []);
    return (
        <div id="page-about" className="container ">
            <PageHeader logoAnimation={scroll}></PageHeader>
            <main>
                <div id="page-about-content">
                    <div className="section-about">
                        <div className="section-about-title">
                            <h2>Poltica de Privacidade</h2>
                            <p>
                                A sua privacidade  importante para ns.  poltica da Mode Mveis respeitar a sua privacidade em relao a qualquer informao sua que possamos coletar no site Mode Mveis, e outros sites que possumos e operamos.
                                Solicitamos informaes pessoais apenas quando realmente precisamos delas para lhe fornecer um servio.
                                Fazemos por meios justos e legais, com o seu conhecimento e consentimento.
                                Tambm informamos por que estamos coletando e como ser usado.
                                Apenas retemos as informaes coletadas pelo tempo necessrio para fornecer o servio solicitado. Quando armazenamos dados, protegemos dentro de meios comercialmente aceitveis para evitar perdas e roubos, bem como acesso, divulgao, cpia, uso ou modificao no autorizados.
                                No compartilhamos informaes de identificao pessoal publicamente ou com terceiros, exceto quando exigido por lei.
                                O nosso site pode ter links para sites externos que no so operados por ns.
                                Esteja ciente de que no temos controle sobre o contedo e prticas desses sites e no podemos aceitar responsabilidade por suas respectivas polticas de privacidade.
                                Voc  livre para recusar a nossa solicitao de informaes pessoais, entendendo que talvez no possamos fornecer alguns dos servios desejados.
                                O uso continuado de nosso site ser considerado como aceitao de nossas prticas em torno de privacidade e informaes pessoais.
                                Se voc tiver alguma dvida sobre como lidamos com dados do usurio e informaes pessoais, entre em contacto connosco.
                            </p>
                        </div>
                        <div className="section-about-title">
                            <h2>Poltica de Cookies Mode Mveis</h2>
                        </div>
                        <div className="section-mv">
                            <div className="section-m">
                                <h3>O que so cookies?</h3>
                                <p>Como  prtica comum em quase todos os sites profissionais, este site usa cookies, que so pequenos conjuntos de dados armazenados no dispositivo do utilizador, para melhorar sua experincia. Esta pgina descreve quais informaes eles coletam, como as usamos e por que s vezes precisamos armazenar esses cookies. Tambm compartilharemos como voc pode impedir que esses cookies sejam armazenados, no entanto, isso pode fazer o downgrade ou 'quebrar' certos elementos da funcionalidade do site.</p>
                            </div>
                            <div className="section-m">
                                <h3>Como usamos os cookies?</h3>
                                <p>Utilizamos cookies por vrios motivos, detalhados abaixo. Infelizmente, na maioria dos casos, no existem opes padro do setor para desativar os cookies sem desativar completamente a funcionalidade e os recursos que eles adicionam a este site.  recomendvel que voc deixe todos os cookies se no tiver certeza se precisa ou no deles, caso sejam usados para fornecer um servio que voc usa.</p>
                            </div>
                            <div className="section-m">
                                <h3>Desativar cookies?</h3>
                                <p>Voc pode impedir a configurao de cookies ajustando as configuraes do seu navegador (consulte a Ajuda do navegador para saber como fazer isso). Esteja ciente de que a desativao de cookies afetar a funcionalidade deste e de muitos outros sites que voc visita. A desativao de cookies geralmente resultar na desativao de determinadas funcionalidades e recursos deste site. Portanto,  recomendvel que voc no desative os cookies.</p>
                            </div>
                        </div>
                        <div className="section-about-title">
                            <h2>Cookies que definimos</h2>
                        </div>
                        <div className="section-mv">
                            <div className="section-m">
                                <h3>Cookies de preferncias do site</h3>
                                <p>Para proporcionar uma tima experincia neste site, fornecemos a funcionalidade para definir suas preferncias de como esse site  executado quando voc o usa. Para lembrar suas preferncias, precisamos definir cookies para que essas informaes possam ser chamadas sempre que voc interagir com uma pgina for afetada por suas preferncias.</p>
                            </div>
                            <div className="section-m">
                                <h3>Cookies relacionados a formulrios</h3>
                                <p>Quando voc envia dados por meio de um formulrio como os encontrados nas pginas de contato ou nos formulrios de comentrios, os cookies podem ser configurados para lembrar os detalhes do usurio para correspondncia futura.</p>
                            </div>
                        </div>
                        <div className="section-about-title">
                            <h2>Em alguns casos especiais, tambm usamos cookies fornecidos por terceiros confiveis. A seo a seguir detalha quais cookies de terceiros voc pode encontrar atravs deste site.</h2>
                        </div>
                        <div className="section-mv">
                            <div className="section-m">
                                <h3>Google Analytics</h3>
                                <p>Este site usa o Google Analytics, que  uma das solues de anlise mais difundidas e confiveis da web, para nos ajudar a entender como voc usa o site e como podemos melhorar sua experincia. Esses cookies podem rastrear itens como quanto tempo voc gasta no site e as pginas visitadas, para que possamos continuar produzindo contedo atraente.</p>
                            </div>
                            <div className="section-m">
                                <p>As anlises de terceiros so usadas para rastrear e medir o uso deste site, para que possamos continuar produzindo contedo atrativo. Esses cookies podem rastrear itens como o tempo que voc passa no site ou as pginas visitadas, o que nos ajuda a entender como podemos melhorar o site para voc.</p>
                            </div>
                        </div>
                        <div className="section-about-title">
                            <h2>Esperemos que esteja esclarecido e, como mencionado anteriormente, se houver algo que voc no tem certeza se precisa ou no, geralmente  mais seguro deixar os cookies ativados, caso interaja com um dos recursos que voc usa em nosso site.</h2>
                        </div>
                        <div className="section-about-link">
                            <Link>Poltica de privacidade  revista em 01 de novembro 2022</Link>
                        </div>
                    </div>
                </div>
            </main>
            <PageFooter />
        </div>
    )
}

export default AboutUs;