import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

import PageHeader from '../../components/PageHeader';
import PageFooter from '../../components/PageFooter';

import './styles.css';

function AboutUs() {
    const [scroll, setScroll] = useState('hidden');
    useEffect(() => {
        const scrollListener = () => {
            if (window.scrollY < 60) {
                setScroll(true);
            } else {
                setScroll(false);
            }
        };
        window.addEventListener("scroll", scrollListener);

        return () => {
            window.removeEventListener("scroll", scrollListener);
        };
    }, []);
    return (
        <div id="page-about" className="container ">
            <PageHeader logoAnimation={scroll}></PageHeader>
            <main>
                <div id="page-about-content">
                    <div className="section-about">
                        <div className="section-about-title">
                            <h2>um pouco sobre a mode mveis</h2>
                            <p>Localizado em Bento Gonalves, na Serra gacha, um dos polos brasileiros mais expressivos do setor moveleiro, 
                                a Mode trabalha com a fabricao de mveis e peas de decorao em ao carbono. Uma empresa que prima pela qualidade 
                                e inovao dos produtos que atendem as mais diversificadas tendncias comerciais e residenciais, 
                                com foco principal em oferecer o melhor em produto e qualidade a seus clientes. 
                                {/*<br /><br />
                                O cuidado com o meio ambiente tambm  prioridade para ns, com isso a madeira utilizada em nossos produtos so certificadas e fruto de reflorestamento, 
                                alm de no processo industrial, evitar qualquer tipo de poluio atravs de resduos que podem prejudicar o nosso bem estar e a natureza.
                                <br /><br />*/}
                            </p>
                        </div>
                        <div className="section-mv">
                            <div className="section-m">
                                <h1>Quem</h1>
                                <h3>Somos?</h3>
                                <p>A Mode  uma empresa jovem inspirada em produzir produtos com paixo e dedicao para iluminar ainda mais os lares dos nossos clientes</p>
                            </div>
                            <div className="section-m">
                                <h1>O que</h1>
                                <h3>Fazemos?</h3>
                                <p>Criamos, produzimos e vendemos mveis complementares de bom design</p>
                            </div>
                            <div className="section-m">
                                <h1>Como</h1>
                                <h3>Adquirir?</h3>
                                <p>Preencha o formulrio em CONTATO e inicie uma conversa via WhatsApp com nosso comercial. Ficaremos felizes em poder lhe atender e fazer o melhor possvel para que voc se sinta em casa</p>
                            </div>
                        </div>
                        <div className="section-mv">
                            <div className="section-m">
                                <h1>Misso</h1>
                                <p>Oferecer a nossos clientes o que tem de melhor em mveis complementares e de decorao para proporcionar aconchego com design, qualidade e sofisticao em nossos produtos</p>
                            </div>
                            <div className="section-m">
                                <h1>Viso</h1>
                                <p>Ser uma empresa inovadora que esta sempre a frente entregando produtos que se adequem em todos os tipos de ambientes com a melhor qualidade possvel</p>
                            </div>
                            <div className="section-m">
                                <h1>Valores</h1>
                                <p>Comprometimento, Qualidade, Eficincia, Inovao, Transparncia, Prazer em servir nossos clientes</p>
                            </div>
                        </div>
                        <div className="section-about-link">
                            <Link to="contact">contate-nos</Link>
                        </div>
                    </div>
                </div>
            </main>
            <PageFooter />
        </div>
    )
}

export default AboutUs;